import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import cargoandlogisticsbanner from "./res/cargoandlogisticsbanner.webp";

function IndustriesCargoAndLogistics() {
    return (
        <IndustriesSubTemplate
            bannerh1="Cargo & Logistics"
            bannerp="The cargo and logistics sector generates high amounts of data globally. The international supply chain results in extreme volumes of largely unstructured data thus the high value potential of AI in logistics and transportation."
            bannerImgSrc={cargoandlogisticsbanner}
            bannerImgAlt="Cargo"
            underBanner="AI-Driven Optimization Increases Revenue and Efficiency"
            solutions={[
                ["Predictive Maintenance", "Edammo-enabled AI analyzes and predicts when machine failure is most likely to occur. Predictive insights can determine based on data how many days; a ship, truck, or mechanical equipment are from malfunctioning."],
                ["Supply Chain Optimization", "By analyzing various factors including: external costs, consumer behavior, market data, and suppliers; Edammo can predict with a high degree of accuracy best supply chain optimization of recommended delivery routes."],
                ["Dynamic Pricing", "Pricing analysis is determines based on real-time price changes. These include: shipments disruptions, weather delays, fuel cost changes and other price-sensitive factors. This data is analyzed in real-time to generate the optimal pricing insights."],
            ]}
            footer="AI will continue to transform transportation to make it safer and more efficient"
            footerCred="Tina Quigley, CEO of the Regional Transportation Commission of Southern Nevada (RTC)"
        />
    )
}

export default IndustriesCargoAndLogistics
