import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import retailbanner from "./res/retailbanner.webp";

function IndustriesRetail() {
    return (
        <IndustriesSubTemplate
            bannerh1="Retail"
            bannerp="Data-driven insights are critical for  identifying bottlenecks and finding new opportunities. The push to digitalize the shopping experience has resulted in organizations integrating AI capabilities into the retail sector at an accelerated rate."
            bannerImgSrc={retailbanner}
            bannerImgAlt="Buying cereal"
            underbanner="Enhance Product and Service Development with Enterprise AI"
            solutions={[
                ["Customer Service Analytics", "Machine learning driven analytics from customer calling and surveying produces actionable insights that identify key customer problems. These actionable insights enable organizations to fine tune their product to deliver better customer satisfaction."],
                ["Pricing Optimization", "AI-customer analytics provides organizations with intelligence regarding future buying behaviors with a high degree of accuracy. This predictive analytics of positive impact customers is coupled with factors that inhibit customers from buying products. Thereby producing dynamic and actionable inisghts"],
                ["Sales and Demand Forecasting", "Highly accurate AI-driven demand forecasting can reduce supply chain errors by 50-70% as well as prevent stock overflow of unsold items. Data-driven stock management and forecasting can potentially save organizations *billions of dollars worth of lost revenue. *Capgemini 2020"],
            ]}
            footer="As we make investments in technology and learn how to put artificial intelligence in store [for example], we want our people to live through that and help contribute to that. We want them to be part of the process."
            footerCred="Doug McMillon, Walmart CEO and president"
        />
    )
}

export default IndustriesRetail
