import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import pharmaceuticalbanner from "./res/pharmaceuticalbanner.webp";

function IndustriesPharmaceutical() {
    return (
        <IndustriesSubTemplate
            bannerh1="Pharmaceuticals"
            bannerp="Edammo generates insights from the high volume of patient data, clinical trials, and claims. Machine learning technology predicts efficacy of novel pharmaceuticals, and participation rates of clinical trial subjects."
            bannerImgSrc={pharmaceuticalbanner}
            bannerImgAlt="Pharmacist working"
            underBanner="AI-Enabled Intelligent Clinical Trials enhances likelihood of Trial Success"
            solutions={[
                ["Intelligent Clinical Trial", "Data-driven clinical trial design utilizes Edammo extreme machine learning to analyze past  data  on clinical trials. Thereby, creating an AI-enabled model for the structure of a clinical trial with the highest likelihood of success."],
                ["Patient Engagement", "Using Edammo machine learning to drive the patient selection process is a highly effective method to ensure the ideal patients are chosen to participate in clinical trials."],
                ["Real-Time Feedback Monitoring", "Once the clinical trials are underway, Edammo generates real-time analysis of data that enables our client to adjust the parameters of the study. Additionally, our AI technology self-learns thus improving insights throughout the trial."],
            ]}
            footer="Eventually, doctors will adopt AI and algorithms as their work partners. This leveling of the medical knowledge landscape will ultimately lead to a new premium: to find and train doctors who have the highest level of emotional intelligence."
            footerCred="Eric Topol, Deep Medicine: How Artificial Intelligence Can Make Healthcare Human Again"
        />
    )
}

export default IndustriesPharmaceutical
