import React from 'react'
import CaseStudiesSubTemplate from './CaseStudiesSubTemplate';
import stressed_m from "./res/stressed_m.webp";
import ferreira from "./res/ferreira.webp";
import thumb from "./res/thumb.webp";


function CaseStudiesImageRecognitionTech() {
    /**(underneath) edammo 65% correct object detection, vs 30% from the google baseline*/

    return (
        <CaseStudiesSubTemplate
            bannerh1="Our Work"
            bannerp="Edammo helps our clients at a Recycling Operations Center to enhance their sorting process."
            fleximg1={stressed_m}
            flexhn1="The Challenge"
            flexText1={<ul>
                <li>Client automates recycling center operations by using an overhead camera to find recyclable items on a conveyor belt</li>
                <li>The images were cluttered from the camera resulting in only a 30% detectable rate using Google’s Auto ML framework based on Deep Learning and limited data</li>
            </ul>}
            fleximg2={ferreira}
            flexhn2="The Solution"
            flexText2={<p>Edammo developed an object detection front end application that leverages the Edammo engine and detects four types of recyclable object categories making detection decisions in real time</p>}
            fleximg3={thumb}
            flexhn3="Results"
            flexp3={<>Edammo <span className="caseStudiesSub_perc">65%</span> correct object detection, vs <span className="caseStudiesSub_perc">30%</span> from the Google baseline.</>}
        />
    )
}

export default CaseStudiesImageRecognitionTech
