import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import travelandtourismbanner from "./res/travelandtourismbanner.webp";

function IndustriesTravelAndTourism() {
    return (
        <IndustriesSubTemplate
            bannerh1="Travel & Tourism"
            bannerp="The future of travel and tourism is through the ability to harness AI in a dynamic market. Edammo machine learning can be applied to various industries in the travel industry to gain a competitive advantage."
            bannerImgSrc={travelandtourismbanner}
            bannerImgAlt="Railroad"
            underBanner="Future tourism depends on AI-enabled analytics for enterprise and consumer protection"
            solutions={[
                ["Personalized Tourism", "Edammo enhances the user experience by creating machine-learning-driven personalized recommendations for users based on past data and user preferences. Using AI to personalize the travel experience of the user enables an optimal AI-driven travel experience."],
                ["Intelligent Contingency Management", "Predictive analytics to determine future tourism disruptions (travel disruptions, weather delays, and flight cancellations) enable contingency plans for travelers.  Managing disruptions through carefully crafted contingency plans empowers travelers to travel confidently."],
                ["Dynamic Pricing", "Machine learning applications enables hotel chains, activity providers, and transportation companies to determine optimal prices based on predictive analytics. Real-time feedback allows for dynamic pricing."],
            ]}
            footer="Machine learning allows us to build software solutions that exceed human understanding and shows us how AI can innervate every industry."
            footerCred="Steve Jurvetson, Board Member of SpaceX and Tesla"
        />
    )
}

export default IndustriesTravelAndTourism
