import React from 'react'
import CaseStudiesSubTemplate from './CaseStudiesSubTemplate';
import stressed from "./res/stressed.webp";
import brucemars from "./res/brucemars.webp";
import lightbulb from "./res/lightbulb.webp";

function CaseStudiesMarketingTech() {
    /**conversion rate went from 20% to 29% (a 45% increase), and was rolled out within 3 weeks */

    return (
        <CaseStudiesSubTemplate
            bannerh1="Our Work"
            bannerp="How Edammo Analytics Supports Lead Generation and Ad-Targeting"
            fleximg1={stressed}
            flexhn1="The Challenge"
            flexText1={<p>The client runs a lead generation call center selling medical insurance. The average call conversion rate is 20% which was not on target for the companies cost and revenue goals. Additionally, the training data was very limited and existing prediction models did not improve the 20% call conversion sampling.</p>}
            fleximg2={brucemars}
            flexhn2="The Solution"
            flexText2="Using demographic data, Edammo developed a model to more accurately identify buyers vs non-buyers and focus call center targeting."
            fleximg3={lightbulb}
            flexhn3="The Results"
            flexp3={<>Conversion rate went from <span className="caseStudiesSub_perc">20%</span> to <span className="caseStudiesSub_perc">29%</span> (a <span className="caseStudiesSub_perc">45%</span> increase), and was rolled out within 3 weeks.</>}
        />
    )
}

export default CaseStudiesMarketingTech
