import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import telecommunicationsbanner from "./res/telecommunicationsbanner.webp";

function IndustriesTelecommunications() {
    return (
        <IndustriesSubTemplate
            bannerh1="Telecommunications"
            bannerp="AI-enabled data driven insights to predict consumer buying patterns."
            bannerImgSrc={telecommunicationsbanner}
            bannerImgAlt="Signal tower"
            underBanner="AI-enable analytics for telecommunications ensures enterprises can strategically allocate their resources"
            solutions={[
                ["Pricing and Promotion", "Identify and drive sales by determining the optimal price and data-driven promotional offers for customers. AI-enabled personalization allows telecommunication firms to have enhanced and more targeted marketing strategies."],
                ["Asset Planning", "Data-based analysis capture the value current usage and generate AI-model-driven demand forecasts. Most importantly, workflow and process automation are key to transforming data into actionable insights."],
                ["Productivity Analysis", "Enhance customer experience while reducing operational costs with AI-enabled field-staff scheduling. Digital tools collect and analyze data to determine bottlenecks and find best course of action."],
            ]}
            footer="Digital technology, pervasively, is getting embedded in every place: every thing, every person, every walk of life is being fundamentally shaped by digital technology — it is happening in our homes, our work, our places of entertainment."
            footerCred="Satya Nadella, CEO of Microsoft"
        />
    )
}

export default IndustriesTelecommunications
