import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import oilandgasbanner from "./res/oilandgasbanner.webp";

function IndustriesOilAndGas() {
    return (
        <IndustriesSubTemplate
            bannerh1="Oil & Gas"
            bannerp="Edammo AI helps oil & gas firms integrate existing data, operational sources, and sensor networks, to extreme machine learning models that generate highly accurate predictive insights enterprise wide."
            bannerImgSrc={oilandgasbanner}
            bannerImgAlt="Oil rig"
            underBanner="Unlock full value of Data and Leverage cost-saving Business Insights"
            solutions={[
                ["Process Optimization", "Our data analytics technology helps companies detect and identify process degradation and reduce waste. Optimize your business process through machine learning driven feedback and performance optimization recommendations based on operational parameters. Business can leverage machine learning generated recommendations that prioritize safety, production, runtime, and yield-risk issues."],
                ["Network Flow and Monitoring", "Machine learning technology accurately identifies operational deviations from predicted flows, alert generation, and the reduction of bottlenecks. The analytics generated at the field and network enable users to automate production and optimize operation time-series data."],
                ["Machine Learning and Safety", "Edammo deploys machine learning technology to identify and detect safety hazards in real time. Based on real-time video feedback, Edammo image recognition technology analyzes actual safety parameters. Highly sophisticated object detection capabilities can be deployed onto all field devices for rapid analysis and response time"],
            ]}
            footer="The AI in Oil and Gas market was valued at USD2 billion in 2019 and is expected to reach USD3.81 billion by 2025, at a CAGR of 10.96% over the forecast period 2020-2025"
            footerCred="ResearchandMarkets.com"
        />
    )
}

export default IndustriesOilAndGas
