import React from 'react'
import { Link } from 'react-router-dom';
import "./Careers.css";
import "./CareersMobile.css";
import dev from "./res/dev.webp";
import res from "./res/res.webp";
import anal from "./res/anal.webp";

function Careers() {
    return (
        <div className="careers">

            {/**BANNER */}
            <div className="careersBanner">
                <h1>Careers</h1>
            </div>

            {/**ROLES */}
            <div className="careersRoles">

                <Link className="careersRoles_card" to="/careers/developer">
                <div>
                    <img src={dev} alt="Developer"></img>
                    <Link to="/careers/developer">
                        <button className="careersRoles_role">Developer</button>
                    </Link>
                    <h3>Senior Software Developer</h3>
                    <Link to="/careers/developer">
                        <button className="careersRoles_applyNow">Apply Now</button>
                    </Link>
                </div>
                </Link>

                <Link className="careersRoles_card careersRoles_cardMiddle" to="/careers/researcher">
                <div>
                    <img src={res} alt="ML Research Scientist"></img>
                    <Link to="/careers/researcher">
                        <button className="careersRoles_role">Researcher</button>
                    </Link>
                    <h3>Machine Learning Research Scientist</h3>
                    <Link to="/careers/researcher">
                        <button className="careersRoles_applyNow">Apply Now</button>
                    </Link>
                </div>
                </Link>

                <Link className="careersRoles_card" to="/careers/marketing">
                <div>
                    <img src={anal} alt="Marketing"></img>
                    <Link to="/careers/marketing">
                        <button className="careersRoles_role">Marketing</button>
                    </Link>
                    <h3>Marketing Associate</h3>
                    <Link to="/careers/marketing">
                        <button className="careersRoles_applyNow">Apply Now</button>
                    </Link>
                </div>
                </Link>

            </div>

        </div>
    )
}

export default Careers
