import React, { useState } from 'react';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import "./WhitePaper.css";
import "./WhitePaperMobile.css";
import whitepaperpdf from "./res/EdammoWhitePaperDec21.pdf"; // "./res/EdammoWhitePaper.pdf"

function WhitePaper() {

  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

    return (
        <div className="benchmarks">
            <div className="benchmarksContainer">
            <a href={whitepaperpdf} download>
                <div className="benchmarksDownload">
                    Download the White Paper
                </div>
            </a>
            <div className="benchmarksPDFViewer">
                <div className="PDFViewer">
                <Document file={whitepaperpdf} onLoadSuccess={onDocumentLoadSuccess}>
                    <Page pageNumber={pageNumber} />
                </Document>
                </div>
                <div className="paginationViewer">
                <button className="paginationButton" disabled={pageNumber === 1} onClick={() => setPageNumber(pageNumber - 1)}>
                    Previous
                </button>
                <button className="paginationButton" disabled={pageNumber === numPages} onClick={() => setPageNumber(pageNumber + 1)}>
                    Next
                </button>
                </div>
            </div>
            </div>
        </div>
    )
}

export default WhitePaper
