import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import utilitiesbanner from "./res/utilitiesbanner.webp";

function IndustriesUtilities() {
    return (
        <IndustriesSubTemplate
            bannerh1="Utilities"
            bannerp="Patent-pending algorithms capture the value of energy grids to generate data-driven predictive analytics."
            bannerImgSrc={utilitiesbanner}
            bannerImgAlt="People fixing an electric box"
            underBanner="Real-time data increases revenue through predictive management and planning"
            solutions={[
                ["Customer Usage Analysis", "Using machine learning algorithms, Edammo enables utility teams to build applications that enhance the customer journey by leveraging; energy usage information and personalized energy savings recommendations, promote new products and services."],
                ["Grid Investment Planning", "Edammo machine learning provides utility infrastructure teams with big data analytics and predictive modeling to  generate data-driven investment plans that factor in various financial constraints, safety and reliability goals."],
                ["Energy Resource Management", "Use machine learning to optimize management of distributed energy resources by integrating all real-time data across systems and sensor networks. Edammo enables utility firms to access secure critical data for a utility’s grid edge control and operational platforms."],
            ]}
            footer="83% of top European utilities executives consider artificial intelligence a high to medium priority for their business"
            footerCred="Roland Berger, 2018"
        />
    )
}

export default IndustriesUtilities
