import React from 'react'
import CareersSubTemplate from './CareersSubTemplate';

function CareersAnalyst() {
    return (
        <CareersSubTemplate
            header="Marketing Associate"
            bannerbg="anal"
            rolelist={[
                "Work with senior management to define and execute a strategic sales plan",
                "Form close, trust-based relationships with industry clients",
                "Undertake client mapping exercises within key industry sectors relevant to Edammo’s coverage areas: Artificial Intelligence, Robotics and Advanced Computing",
                "Achieve new business revenue targets, proactively responding to inbound inquiries and self-generated leads based on a deep market and product understanding",
                "Act as the primary point of contact for clients, ensuring timely and successful delivery of Edammo’s solutions according to customer needs and objectives",
                "Work collaboratively with Edammo’s dedicated team to promote Edammo’s capabilities, solutions and ambitions across the event portfolio",
                "Report accurate forecasting and performance metrics to key internal and external stakeholders, highlighting revenue risks and opportunities",
                "Attend key Edammo Tech and industry relevant competitor events",
            ]}
        />
    )
}

export default CareersAnalyst
