import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import humanresourcesbanner from "./res/humanresourcesbanner.webp";

function IndustriesHumanResources() {
    return (
        <IndustriesSubTemplate
            bannerh1="Human Resources"
            bannerp="Edammo machine learning enables recruiters to automate the candidate filtering process thereby allowing recruiters to allocate their time more strategically."
            bannerImgSrc={humanresourcesbanner}
            bannerImgAlt="Taking Notes"
            underBanner="Increase accuracy and candidate processing while simultaneously saving time and cutting costs"
            solutions={[
                ["Automated Candidate Screening", "By automating initial candidate screenings, recruiters can re-strategize  their task allocation. Thereby, allowing human resource departments to focus on more high-level tasks and projects."],
                ["Workforce Analytics", "Edammo machine learning analyzes employee and candidate behaviors to generate recommendations to improve business operations and human performance."]
            ]}
            footer="As recruiters continue to hire leavers the machine will learn. It will learn about the inefficiencies in humans hiring humans. It will learn which humans do a good job vs. those that don't."
            footerCred="Peter Gold, HCM Technology Analyst"
        />
    )
}

export default IndustriesHumanResources
