import React, { useRef } from 'react';
import "./GetStarted.css";
import "./GetStartedMobile.css";
import GlobeAnimation from "../../components/GlobeAnimation/GlobeAnimation";

function GetStarted() {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    const contact_info = {
        'firstname': e.target.firstname.value,
        'lastname': e.target.lastname.value,
        'email': e.target.email.value,
        'phone': e.target.phone.value,
        'message': e.target.message.value,
    }
    if (contact_info['firstname'] === '') { contact_info['firstname'] = '.' }
    if (contact_info['lastname'] === '') { contact_info['lastname'] = '.' }
    if (contact_info['email'] === '') { contact_info['email'] = '.' }
    if (contact_info['phone'] === '') { contact_info['phone'] = '.' }
    if (contact_info['message'] === '') { contact_info['message'] = '.' }

    fetch('https://api.edammoinc.com/api/contact', {
        method: 'POST',
        body: JSON.stringify(contact_info),
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json'
        }
    })
    alert(`Thank you for your message!`);
  };


    return (
        <div className="getStarted">

            {/**BANNER */}
            <div className="getStartedBanner">
                <h1>Get Started</h1>
                <p>Let’s give your business what it needs to grow.</p>
            </div>

            {/**BODY */}
            <div className="getStartedBody">
                <div className="getStartedBody_left">
                    <GlobeAnimation width={400}/>
                    <p>We look forward to working with you.</p>
                </div>
                <div className="getStartedBody_right">
                    <form ref={form} action="/" method="get" onSubmit={sendEmail}>
                        <h2>Contact Us</h2>
                        <div className="getStartedBody_formTop">
                            <label htmlFor="firstname">First Name<br/>
                                <input name="firstname" id="firstname" placeholder="First Name"/>
                            </label>
                            <label htmlFor="lastname">Last Name<br/>
                                <input name="lastname" id="lastname" placeholder="Last Name"/>
                            </label>
                        </div>
                        <div className="getStartedBody_formBottom">
                            <label htmlFor="email">Email<br/>
                                <input name="email" id="email" type="email" placeholder="Email"/>
                            </label>
                            <label htmlFor="phone">Phone<br/>
                                <input name="phone" id="phone" type="tel" placeholder="Phone"/>
                            </label>
                        </div>
                        <label className="getStartedBody_message" htmlFor="messaage">Leave us a message...<br/>
                            <textarea name="message" id="message" placeholder="Type your message here..."></textarea>
                        </label>
                        <button className="getStartedBody_submit">Submit</button>
                    </form>
                </div>
            </div>
            
        </div>
    )
}

export default GetStarted
