import React from 'react'
import IndustriesSubTemplate from "./IndustriesSubTemplate";
import marketingbanner from "./res/marketingbanner.webp";

function IndustriesMarketing() {
    return (
        <IndustriesSubTemplate
            bannerh1="Marketing"
            bannerp="Edammo AI algorithms analyze information and improve automatically through machine learning technology. Leveraging machine learning enables companies to target customer segments with a data-driven approach."
            bannerImgSrc={marketingbanner}
            bannerImgAlt="Deskwork"
            underBanner="Increase revenue and Cut Costs through Targeted Customer Marketing"
            solutions={[
                ["Automation", "Marketing automation enables the translation of data into empowered decision making.  Real-time machine learning  can be adjusted in real-time with no human supervision required."],
                ["Marketing Automation", "In order to leverage AI and machine learning, a range of factors including; services, products, information, content, and messages determine optimal customer segmentation. Companies and users can make data-driven decisions with Edammo machine learning."],
                ["Predictive Marketing Analytics", "Data sets, models, algorithms, and machine learning enable companies to generate predictive analytics regarding consumer behavior.  This enables marketing teams to be data-driven when positioning products."],
            ]}
            footer="Eighty percent of what we do every day will be intelligently automated to some degree in the next three to five years."
            footerCred="Paul Roetzer, Founder and CEO of Marketing Artificial Intelligence Institute and PR 20/20"
        />
    )
}

export default IndustriesMarketing
