import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import manufacturingbanner from "./res/manufacturingbanner.webp";

function IndustriesManufacturing() {
    return (
        <IndustriesSubTemplate
            bannerh1="Manufacturing"
            bannerp="We enable manufacturers to translate their data into actionable insights. Predictive maintenance, quality control, defect optimization help manufacturers identify bottlenecks while reducing economic losses."
            bannerImgSrc={manufacturingbanner}
            bannerImgAlt="Machinery"
            underBanner="Cut costs and transform Business Strategy from Reactive to Proactive"
            solutions={[
                ["Assembly Line Optmization", "Edammo machine learning detects in real-time when equipment is malfunctioning or slowing down. furthermore if machinery loses functionality, Edammo machine learning will automatically enable contingency systems."],
                ["Defect Detection", "Machine learning and AI enables manufacturers to reduce need for employer on-site quality control. Machine learning can differentiate between functional and detective machinery thereby resulting in fewer false-positives."],
                ["Quality Assurance", "AI-enabled object detection technology detects whether an item has been manufactured correctly or is flawed. Automatic sensors provide real-time feedback enabling manufactures to respond quickly and effectively."],
            ]}
            footer="Artificial intelligence and machine learning, as a dominant discipline within AI, is an amazing tool. In and of itself, it’s not good or bad. It’s not a magic solution. It isn’t the core of the problems in the world."
            footerCred="Vivienne Ming, Executive Chair & Co-Founder, Socos Labs"
        />
    )
}

export default IndustriesManufacturing
