import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import insurancebanner from "./res/insurancebanner.webp";

function IndustriesInsurance() {
    return (
        <IndustriesSubTemplate
            bannerh1="Insurance"
            bannerp="The next decade of insurance will be defined by automation and machine learning. An insurance firms' ability to stay ahead of the curve depends on their organizational digital change management capabilities."
            bannerImgSrc={insurancebanner}
            bannerImgAlt="Insurance"
            underBanner="Increase revenue and save time experience through automation and machine learning"
            solutions={[
                ["Personalized Insurance", "More and more customers seek personalized insurance plans suited to their unique circumstances. Therefore, data-driven insurance plans based on the customer portfolio will enable insurance providers to add the most value to their customer."],
                ["Pricing Analytics", "Consumer data generated profiles enable Edammo to create predictive models of customer risk. Thereby producing recommended pricing models based on riskier and less riskier customers."],
                ["Automated Claim Response", "Machine learning in conjunction with automation enables instantaneous claim processing. For example, a customer can file a claim or appeal and receive an automated decision within hours of filing."],
            ]}
            footer="The [insurance] industry is on the verge of a seismic, tech-driven shift."
            footerCred="Mckinsey 2021"
        />
    )
}

export default IndustriesInsurance
