import React from 'react'
import "./Industries.css";
import "./IndustriesMobile.css";
import IndustriesCard from './IndustriesCard';
import aviation from "./res/aviation.jpg";
import cargoandlogistics from "./res/cargoandlogistics.jpg";
import healthcare from "./res/healthcare.jpg";
import humanresources from "./res/humanresources.jpg";
import insurance from "./res/insurance.jpg";
import manufacturing from "./res/manufacturing.jpg";
import marketing from "./res/marketing.jpeg";
import oilandgas from "./res/oilandgas.jpg";
import pharmaceutical from "./res/pharmaceutical.jpg";
import retail from "./res/retail.jpg";
import telecommunications from "./res/telecommunications.jpg";
import utilities from "./res/utilities.jpg";

function Industries() {
    //const retail = "https://www.zebra.com/content/dam/zebra_new_ia/en-us/solutions-verticals/vertical-solutions/retail/photography-website/retail-photography-website-grocery-store-1x1-300x300.jpg";

    return (
        <div className="industries">
            {/**BANNER */}
            <div className="industriesBanner">
                <div className="industriesBanner_bottom">
                    <h1>Industries</h1>
                    <div className="industriesBanner_bottomHr"></div>
                    <p>Edammo is real world artificial intelligence putting business insights at your fingertips every day. We help you make the right business decisions using our innovative technology that derives accurate, real-time insights from even the smallest and most dynamic data sets. Versatile technology allows for flexible usage via API or custom built interfaces or apps and adaptable architecture ensures you can use in cloud, hybrid or local applications or flex as your business grows. How are you making data driven decisions today?</p>
                </div>    
                <div className="industriesBanner_top"></div>
            </div>

            {/**CARDS */}
            <div className="industriesCards">
                <IndustriesCard
                    imgSrc={aviation}
                    text="Aviation"
                    href="/industries/aviation"
                />
                <IndustriesCard
                    imgSrc={cargoandlogistics}
                    text="Cargo & Logistics"
                    href="/industries/cargoandlogistics"
                />
                <IndustriesCard
                    imgSrc={healthcare}
                    text="Healthcare"
                    href="/industries/healthcare"
                />
                <IndustriesCard
                    imgSrc={humanresources}
                    text="Human Resources"
                    href="/industries/humanresources"
                />
                <IndustriesCard
                    imgSrc={insurance}
                    text="Insurance"
                    href="/industries/insurance"
                />
                <IndustriesCard
                    imgSrc={manufacturing}
                    text="Manufacturing"
                    href="/industries/manufacturing"
                />
                <IndustriesCard
                    imgSrc={marketing}
                    text="Marketing"
                    href="/industries/marketing"
                />
                <IndustriesCard
                    imgSrc={oilandgas}
                    text="Oil & Gas"
                    href="/industries/oilandgas"
                />
                <IndustriesCard
                    imgSrc={pharmaceutical}
                    text="Pharmaceutical"
                    href="/industries/pharmaceutical"
                />
                <IndustriesCard
                    imgSrc={retail}
                    text="Retail"
                    href="/industries/retail"
                />
                <IndustriesCard
                    imgSrc={telecommunications}
                    text="Telecommunications"
                    href="/industries/telecommunications"
                />
                <IndustriesCard
                    imgSrc={utilities}
                    text="Utilities"
                    href="/industries/utilities"
                />
            </div>  
        </div>
    )
}

export default Industries
