import React from 'react'
import CareersSubTemplate from './CareersSubTemplate';

function CareersResearcher() {
    return (
        <CareersSubTemplate
            header="Machine Learning Research Scientist"
            bannerbg="res"
            rolelist={[
                "A PhD in computer science or another relevant area providing good foundation in AI, computer science, mathematics, and data science (including statistics, probability theory, etc.)",
                "Comfortable with open-ended research projects",
                "Proven capabilities and several years of experience in the domains of AI and hands-on experience with machine learning including research publications",
                "Experience with programming languages, specifically: Python, C and C++",
                "Good communication and interpersonal skills, with the ability to build strong personal relationships at all levels",
            ]}
        />
    )
}

export default CareersResearcher
