import React, { useEffect, useState } from 'react';
import './OurBlog.css';
import { fetchBaseUrl } from '../../components/.env';
import ReactMarkdown from 'react-markdown';

function OurBlog() {
  const [state, setState] = useState({
    posts: [],
  });

  useEffect(() => {
    fetch([fetchBaseUrl, 'api', 'blogposts?sort=updatedAt:DESC'].join('/'))
      .then((res) => res.json())
      .then((data) => {
        setState({
          ...state,
          posts: data.data.map((datum) => datum.attributes),
        });
        console.log(state);
      })
      .catch((err) => console.log(err));
  }, []); // eslint-disable-line

  return (
    <div className="ourblog">
      <h1>Our Blog</h1>

      {/**POSTS */}
      {state.posts.map((post) => (
        <div className="ourblogPost">
          <h1 className="ourblogPost_title">{post.TITLE}</h1>
          <h2 className="ourblogPost_date">
            Published {new Date(post.publishedAt).toDateString()}
          </h2>
          <ReactMarkdown>{post.ARTICLE}</ReactMarkdown>
        </div>
      ))}
    </div>
  );
}

export default OurBlog;
