import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import "./StockGraph.css";

function StockGraph({url, title}) {
  // chartjs Config Goodness

  const [state, setState] = useState({
    given: [],
    predicted: [],
    labels: [],
  });

  const GIVEN_COLOR = 'blue';
  const PREDICTED_COLOR = 'red';

  useEffect(() => {
    fetch(url)
    .then(res => res.json())
    .then(data => {
      const given = [...data.actual_values.map(p => p.value), undefined]; // actual/given does not have a last point
      const predicted = [undefined, ...data.predicted_values.map(p => p.value)]; // predicted does not have a first point
      const labels = [...new Set([
        ...data.actual_values.map(p => p.date),
        ...data.predicted_values.map(p => p.date),
      ])];

      setState({given, predicted, labels});
    })
    .catch(err => console.log(err));
  }, []); // eslint-disable-line

  const dataProp = {
    labels: state.labels,
    datasets: [
      {
        label: "Predicted",
        data: state.predicted,
        backgroundColor: PREDICTED_COLOR,
        borderColor: PREDICTED_COLOR,
        pointBorderWidth: 2,
        pointBorderColor: PREDICTED_COLOR,
        pointRadius: 3,
        showLine: false,
        pointHoverBackgroundColor: 'white',
        pointHoverRadius: 8,
        pointHoverBorderWidth: 4,
      },
      {
        label: "Actual",
        data: state.given,
        backgroundColor: GIVEN_COLOR,
        borderColor: GIVEN_COLOR,
        pointBorderWidth: 2,
        pointBorderColor: GIVEN_COLOR,
        pointRadius: 5, // fatter, so it sticks out underneath predicted
        showLine: false,
        pointHoverBackgroundColor: 'white',
        pointHoverRadius: 8,
        pointHoverBorderWidth: 4,
      },
    ],
  };

  const options = {
    animations: { // copied from https://www.chartjs.org/docs/latest/configuration/animations.html for flashy goodness
      tension: {
        duration: 1000,
        easing: 'linear',
        from: 1,
        to: 0,
        loop: true
      }
    },
    plugins: {
      legend: {
        display: false,
      },
      tooltip: {
        displayColors: false,
      },
    },
  };

  return <div className="stockGraph">
    <h3>{title}</h3>
    <Line data={dataProp} options={options} />
  </div>;
}

export default StockGraph;
