import React from 'react'
import IndustriesSubTemplate from './IndustriesSubTemplate';
import aviationbanner from "./res/aviationbanner.webp";

function IndustriesAviation() {
    return (
        <IndustriesSubTemplate
            bannerh1="Aviation"
            bannerp="Enabling innovation and data-driven decision making in aircraft and airport operations. Edammo enabled-AI uses powerful machine learning algorithms for time series analysis, hybrid modeling, and decision making."
            bannerImgSrc={aviationbanner}
            bannerImgAlt="Flight"
            underBanner="Increase speed, efficiency, and safety while cutting costs for Airlines and Airports"
            solutions={[
                ["Flight Operation Analysis", "Edammo machine learning analyzes and detects deviancies in on-flight aircraft data, including exceeding the limitations as stated in the Aircraft Flight Manual. Data is processed in the Edammo machine learning engine allowing airlines to unlock the full value of their in-flight data."],
                ["Predictive Maintenance", "Detect and identify when to anticipate failures or malfunctions.  Edammo uses machine learning to analyze streaming data, detect anomalies and predict malfunctions before they occur.​"],
                ["Real-Time Disruption Management", "Edammo machine learning algorthms improve the response time to airport disruptions. Live video feedback can be processed using Edammo real-time object recognition technology to identify and detect malfunctioning equipment, passenger overflow, and other negative externalities."],
            ]}
            footer="We use AI processes to build more quickly, more interoperable systems."
            footerCred="Jean-Marc Cluzeau, principal advisor to the European Union Aviation Safety Agency"
        />
    )
}

export default IndustriesAviation
