import React, { useRef } from 'react';
import "./CareersSubTemplate.css";

function CareersSubTemplate({
    header,
    bannerbg,
    rolelist,
}) {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    const contact_info = {
        'firstname': e.target.firstname.value,
        'lastname': e.target.lastname.value,
        'email': e.target.email.value,
        'phone': e.target.phone.value,
        'position': e.target.position.value,
        'date': e.target.date.value,
        'resume': e.target.resume.value,
    }

    if (contact_info['firstname'] === '') { contact_info['firstname'] = '.' }
    if (contact_info['lastname'] === '') { contact_info['lastname'] = '.' }
    if (contact_info['email'] === '') { contact_info['email'] = '.' }
    if (contact_info['phone'] === '') { contact_info['phone'] = '.' }
    if (contact_info['position'] === '') { contact_info['position'] = '.' }
    if (contact_info['date'] === '') { contact_info['date'] = '.' }
    if (contact_info['resume'] === '') { contact_info['resume'] = '.' }

    fetch('https://api.edammoinc.com/api/apply', {
        method: 'POST',
        body: JSON.stringify(contact_info),
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json'
        }
    })
    alert(`Thank you for your message!`);
  };
    return (
        <div className="careersSub">

            {/**BANNER */}            
            <div className={`careersSubBanner careersSubBanner--${bannerbg}`}>
                <h1>{header}</h1>
            </div>

            {/**ROLES */}
            <div className="careersSubRoles">
                <h2>Role Description</h2>
                <ul>
                    {rolelist.map(r => <li key={r}>{r}</li>)}
                </ul>
            </div>

            {/**FORM */}
            <form action="/" method="post" ref={form} onSubmit={sendEmail}>
                <h2>Come Work With Us</h2>
                <div className="careersSubForm_inputs">
                    <label htmlFor="firstname">First Name<br/>
                        <input name="firstname" id="firstname" placeholder="First Name"/>
                    </label>
                    <label htmlFor="lastname">Last Name<br/>
                        <input name="lastname" id="lastname" placeholder="Last Name"/>
                    </label>
                    <label htmlFor="email">Email<br/>
                        <input name="email" id="email" type="email" placeholder="Email"/>
                    </label>
                    <label htmlFor="phone">Phone<br/>
                        <input name="phone" id="phone" type="tel" placeholder="Phone"/>
                    </label>
                    <label htmlFor="position">Position you apply for<br/>
                        <select name="position" id="position" placeholder="Select Position">
                            <option>Senior Software Developer</option>
                            <option>Machine Learning Research Scientist</option>
                            <option>Marketing Associate</option>
                        </select>
                    </label>
                    <label htmlFor="date">Available start date *<br/>
                        <input name="date" id="date" type="date" placeholder="Start Date"/>
                    </label>
                    <input className="careersSubForm_resume" name="resume" placeholder="Link to Your Resume"/>
                </div>

                <button className="careersSubForm_apply">Apply Now</button>
            </form>


        </div>
    )
}

export default CareersSubTemplate
