import React, { useState, useEffect } from 'react';
import StockGraph from "./StockGraph";
import "./Stocks.css";
import { fetchBaseUrl } from "../../components/.env";

// chartjs initialization

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

function Stocks() {
  const [datasets, setDatasets] = useState([]);

  useEffect(() => {
    fetch([fetchBaseUrl, "api", "live-datasets"].join('/'))
    .then(res => res.json())
    .then(data => {
      setDatasets(data.data.map(ds => ds.attributes));
    })
    .catch(err => console.log(err));
  }, []) // eslint-disable-line

  return <div className="stocks">
    {/**HEADING */}
    <div className="stocksHeading">
      <h1>Live Results</h1>
      <hr />
      <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
    </div>

    {/**GRAPHS */}
    <div className="stocksGraphs">
      <div className="stocksGraphs_legend">
        <div className="stocksGraphs_blue"></div>
        <p>Actual</p>
        <div className="stocksGraphs_legendSpace"></div>
        <div className="stocksGraphs_red"></div>
        <p>Predicted</p>
      </div>

      {datasets.map(ds => <StockGraph
        key={ds.TITLE}
        title={ds.TITLE}
        url={ds.URL}
      />)}
    </div>

  </div>;
}

export default Stocks;
